  .catalog__link {
    display: flex;
    flex-direction: row-reverse;
    justify-content: start;
    gap: 25px;
    align-items: center;
    color: @basic_orange;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.2;
    text-transform: uppercase;
    cursor: pointer;
    margin-bottom: 25px;
  }

  .catalog__item-link {
    font-size: 12px;
    line-height: 1.2;
  }

@media (min-width: @desktop-width) {
  .catalog__item-link {
    font-size: 16px;
    line-height: 1.2;
  }
}
