  .year__wrapper {
    .wrapper();
    background-color: @ltltlt_gray;
  }

  .year__title {
    display: inline-block;
    position: relative;
    width: 100%;
    font-size: 24px;
    line-height: 1.2;
    text-align: center;
    padding: 5px 20px;

    &--hide {
      &::before {
        content: "";
        position: absolute;
        top: 7px;
        left: 0;
        width: 13px;
        height: 26px;
        background-image: url(../../img/arrow-right.svg);
        background-repeat: no-repeat;
      }

      &::after {
        content: "";
        position: absolute;
        top: 7px;
        right: 0;
        width: 13px;
        height: 26px;
        background-image: url(../../img/arrow-left.svg);
        background-repeat: no-repeat;
      }
    }

    &--show {
      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 12px;
        width: 26px;
        height: 13px;
        background-image: url(../../img/arrow-bottom.svg);
        background-repeat: no-repeat;
      }

      &::before {
        left: 0;
      }

      &::after {
        right: 0;
      }
    }
  }

  .year__list {
    .list();
    margin-top: 35px;
  }

  .year__item {
    font-size: 12px;
    line-height: 1.2;
    padding-bottom: 20px;
  }


@media (min-width: @desktop-width) {
  .year__wrapper {
    display: grid;
    grid-template-columns: 607px 374px;
    column-gap: 50px;
    row-gap: 25px;
    background-color: @basic_white;
  }

  .year__title {
    position: relative;
    display: block;
    padding: 0 45px;
    font-size: 76px;
    text-align: end;

    &--hide,
    &--show {

      &::before,
      &::after {
        display: none;
      }
    }
  }

  .year__list {
    .list();
    margin-top: 35px;
  }

  .year__item {
    font-size: 16px;
    padding-bottom: 20px;
  }
}
