.contacts__wrapper {
  .wrapper();
  margin-bottom: 200px;
}

.contacts__title {
  padding: 50px 20px;
  border-top: 1px solid @lt_gray;
  text-align: center;
}

.contacts__column {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

  .contacts__map {
    position: relative;
  }

  .contacts__company {
    margin: 0;
  }

  .contacts__address {
    font-style: normal;
  }

  .contacts__item {
    display: flex;
    margin-bottom: 20px;
  }

  .contacts__email {
    color: @basic_blue;
  }

  .contacts__general {
    margin-top: 60px;
  }

@media (min-width: @desktop-width) {
  .contacts__wrapper {
    .wrapper();
    margin-bottom: 200px;
  }

  .contacts__title {
    padding: 92px 234px 67px 234px;
    border-top: 1px solid @lt_gray;
  }

  .contacts__column {
    display: flex;
    gap: 50px;
    margin-left: 127px;
    margin-right: 113px;
  }

  .contacts__map {
    position: relative;
  }

  .contacts__company {
    margin: 0;
  }

  .contacts__address {
    font-style: normal;
  }

  .contacts__item {
    display: flex;
    margin-bottom: 20px;
  }

  .contacts__email {
    color: @basic_blue;
  }

  .contacts__general {
    margin-top: 60px;
  }
}
