@media (min-width: @desktop-width) {
  .button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: @font-family--additional;
    font-weight: normal;
    font-size: 48px;
    line-height: 1;
    padding: 6px 10px;
    width: 100%;
    min-height: 60px;
    color: @basic_black;
    cursor: pointer;
    background-color: @basic_white;
    border: none;
  }
}
